.main{
  padding: 1.875em 0 3.125em;
  overflow: hidden;
  &__top{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 17.625em;
  }
  &__logo{
    width: 21.875em;
    margin-right: 10.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  &__desc{
    max-width: 15.625em;
    margin-right: 11.4375em;
  }
}

.main-img{
  margin-top: 3.125em;
  img{
    display: block;
    width: 100%;
  }
}

.catalog,
.imagine,
.inspiration{
  padding: 3.125em 0;
  &__title{
    text-align: center;
  }
}

.catalog{
  &__items{
    margin-top: 3.125em;
    @include grid100gap(1.25em);
  }
}

.catalog-card{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .3125em));
  gap: .625em;
  background-color: $gray;
  align-items: flex-end;
  &__slider{
    position: relative;
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-nav{
        position: absolute;
        right: 3.125em;
        bottom: 3.125em;
        z-index: 2;
      }
    }
  }
  &__img{
    height: 31.25em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 3.125em;
  }
  &__info{
    max-width: 37.5em;
    @include grid100gap(.625em);
  }
  &__desc{
    max-width: 23em;
  }
  @media screen and (min-width: 993px){
    &:nth-child(even){
      .catalog-card__content{
        order: -1;
      }
    }
    ._grid2,
    ._grid3{
      .swiper{
        &-wrapper{
          transform: unset !important;
          display: grid;
          gap: .625em;
        }
        &-slide {
          width: 100% !important;
        }
        &-nav{
          display: none;
        }
      }
    }
    ._grid2{
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .3125em));
        }
        &-slide{
          &:nth-child(2){
            .catalog-card__img{
              &:after{
                left: 0;
                right: unset;
                border-left: unset;
                border-right: 9.375em solid transparent;
              }
            }
            &~.swiper-slide{
              display: none;
            }
          }
        }
      }
    }
    ._grid3{
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .3125em));
          height: 31.25em;
        }
        &-slide{
          .catalog-card__img{
            height: 100%;
          }
          &:nth-child(1){
            grid-row: 1/3;
          }
          &:nth-child(2),
          &:nth-child(3){
            .catalog-card__img{
              &:after{
                display: none;
              }
            }
          }
          &:nth-child(3){
            .catalog-card__img{
              &:after{
                //left: 0;
                //right: unset;
                //border-left: unset;
                //border-right: 9.375em solid transparent;
              }
            }
            &~.swiper-slide{
              display: none;
            }
          }
        }
      }
    }
    ._no-right-triangle{
      .swiper-slide:nth-child(1){
        .catalog-card__img:after{
          left: 0;
          right: unset;
          border-left: unset;
          border-right: 9.375em solid transparent;
        }
      }
      .swiper-slide:nth-child(2){
        .catalog-card__img:after{
          display: none;
        }
      }
    }
  }
}

.imagine{
  .video{
    margin-top: 3.125em;
  }
}

.video{
  display: block;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  video,
  img{
    width: 100%;
    display: block;
  }
  video{
    opacity: 0;
  }
  &__play{
    pointer-events: none;
    width: 12.5em;
    height: 9.375em;
    z-index: 2;
    background: rgba(0, 0, 0, 0.01);
    box-shadow: .0625em .0625em 0 rgba(68, 68, 68, 0.25), inset 1px 1px 0px rgba(181, 181, 181, 0.25);
    backdrop-filter: blur(10px);
    border-radius: 31.25em;
    @include centerAbsolute;
    @include center;
    img{
      width: 3.125em;
    }
  }
  &._played{
    .video__play{
      display: none;
    }
    video{
      opacity: 1;
    }
    .video__img{
      opacity: 0;
    }
  }
}

.inspiration{
  margin-bottom: 2.5em;
  &__grid{
    max-width: calc(100% - 14.6875em);
    margin: 3.75em 0 0 auto;
    padding: 4.375em 3.125em 0 41.875em;
    background-color: $gray;
    position: relative;
  }
  &__img{
    position: absolute;
    top: 4.375em;
    left: -14.6875em;
    width: 53.4375em;
    height: 100%;
    object-fit: cover;
    max-height: calc(100% - 2.5em);
    display: block;
  }
  &__top{
    @include grid100gap(.5625em);
  }
  &__item{
    display: inline-block;
    color: $gray2;
    span,
    &:before{
      font-size: 1.25em;
      line-height: 140%;
      text-transform: uppercase;
      font-family: $font2;
    }
    &:not(:first-child):before{
      content: '/';
      margin: 0 .5em;
    }
  }
  &__wrap{
    padding-left: 4.5625em;
  }
  &__info{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 2em;
      right: 100%;
      margin-right: 1.4375em;
      width: 3.125em;
      height: 1px;
      background-color: $primary;
    }
  }
  &__subtitle{
    font-size: 2.5em;
    line-height: 150%;
    b{
      font-weight: 600;
    }
  }
  &__nav{
    display: flex;
    min-height: 5.875em;
    position: relative;
  }
  &__btn{
    position: absolute;
    left: 0;
    bottom: -2.5em;
  }
}

.philosophy{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    gap: .625em;
  }
  &__title{
    grid-column: 1/3;
    max-width: 7em;
  }
}

.philosophy-card{
  padding: 3.125em 1.875em;
  border: 1px solid #E0E3E4;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    background: #fff;
  }
  &:after{
    width: 1px;
    height: 2em;
    right: -1px;
    top: 0;
  }
  &:before{
    height: 1px;
    width: 2em;
    top: -2px;
    right: 0;
  }
  ._line{
    position: absolute;
    width: 2.98em;
    height: 1px;
    right: -1px;
    top: -1px;
    background-color: #E0E3E4;
    transform: translate(.4em,1em) rotate(45deg);
  }
  @media screen and (min-width: 993px){
    &:nth-child(3),
    &:nth-child(5){
      ._line{
        transform: translate(-.4em,1em) rotate(-45deg);
      }
      ._line,
      &:before,
      &:after{
        right: unset;
        left: -2px;
      }
    }
    &:nth-child(6),
    &:nth-child(7){
      &:after{
        top: unset;
        bottom: 0;
      }
      ._line,
      &:before{
        top: unset;
        bottom: -1px;
      }
    }
    &:nth-child(6){
      ._line{
        transform: translate(.4em,-1em) rotate(-45deg);
      }
    }
    &:nth-child(7){
      &:before{
        right: unset;
        left: 0;
      }
      ._line,
      &:after{
        right: unset;
        left: -2px;
      }
      ._line{
        transform: translate(-.4em,-1em) rotate(45deg);
      }
    }
  }
  &__img{
    height: 6.25em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: 3.125em;
    @include grid100gap(.625em);
  }
}

.address{
  margin-top: 3.125em;
  padding-bottom: 5.25em;
  background: #1D1D1D;
  position: relative;
  .consult{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}

.map{
  height: 37.5em;
}

.consult{
  z-index: 2;
  &__inner{
    padding: 5em 3.125em;
    background: #FFFFFF;
  }
  &__title{
    text-align: center;
  }
  &__form{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}



@media screen and (max-width: 992px){
  .main{
    padding: 1em 0;
    &__top{
      flex-direction: column;
      justify-content: unset;
      padding: 0;
    }
    &__logo{
      display: none;
    }
    &__btn{
      width: 100%;
    }
    &__info{
      flex-direction: column;
      text-align: center;
    }
    &__desc{
      order: 2;
      margin: 1em 0 0;
    }
  }

  .main-img{
    margin-top: 1.25em;
    img{
      height: 16em;
      width: auto;
      margin-left: -2em;
    }
  }

  .catalog,
  .imagine,
  .inspiration{
    padding: 1em 0;
    &__title{
      text-align: left;
    }
  }

  .catalog{
    &__items{
      margin-top: 2em;
    }
  }

  .catalog-card{
    grid-template-columns: 100%;
    gap: 0;
    overflow: hidden;
    &__slider{
      padding-right: .5em;
      .swiper{
        &-container{
          overflow: unset;
        }
        &-slide{
          padding-right: .5em;
        }
        &-nav{
          display: none;
        }
      }
    }
    &__img{
      padding-bottom: 75%;
      height: unset;
      overflow: hidden;
      &:after{
        display: none;
      }
      img{
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    &__content{
      padding: 1.25em;
    }
    &__info{
      gap: .5em;
    }
  }

  .imagine{
    .video{
      margin-top: 1em;
    }
  }

  .video__play{
    font-size: .5em;
  }

  .inspiration{
    margin-bottom: 0;
    &__grid{
      margin-top: 1em;
      margin-left: -1.25em;
      padding: 0;
      max-width: unset;
      width: calc(100% + 2.5em);
    }
    &__img{
      position: static;
      width: 100%;
      height: unset;
      max-height: unset;
    }
    &__content{
      padding: 1.25em;
    }
    &__top{
      gap: .25em;
    }
    &__item{
      display: block;
      span{
        font-size: .75em;
      }
      &:before{
        content: '-' !important;
        margin: 0 .25em 0 0 !important;
        font-size: .75em;
      }
    }
    &__wrap{
      padding: 0;
    }
    &__info{
      margin-top: 1em;
      gap: .5em;
      &:before{
        display: none;
      }
    }
    &__subtitle{
      font-size: 1.125em;
    }
    &__nav{
      min-height: unset;
      margin-top: 1em;
    }
    &__btn{
      position: static;
    }
  }

  .philosophy{
    padding: 1em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
    }
    &__title{
      max-width: unset;
    }
  }

  .philosophy-card{
    padding: 1.25em .625em;
    &:before,
    &:after,
    ._line{
      display: none;
    }
    &__img{
      height: 3.125em;
    }
    &__content{
      margin-top: 1em;
    }
  }

  .address{
    background-color: transparent;
    margin: 0;
    padding: 1em 0 0;
    .consult{
      position: static;
    }
  }

  .map{
    height: 17.5em;
  }

  .consult{
    padding: 1.25em 0;
    &__inner{
      padding: 0;
    }
    &__title{
      text-align: left;
    }
    &__form{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .video__play{
    font-size: .25em;
  }

  .philosophy{
    &__title{
      grid-column: 1/-1;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
