.team {
  padding: 0.625em 0 3.125em;
  &__items {
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 3.125em 1em;
    margin-top: 1.875em;
  }
}

.team-card {
  display: flex;
  align-items: center;
  @media screen and (min-width: 992px) {
    &--100 {
      grid-column: 1/3;
      width: calc(100% + 12.5em);
      margin-left: -6.25em;
      .team-card__img {
        width: 59.6875em;
        &:after {
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          border-top: 6.25em solid #e0e3e4;
          border-left: 6.25em solid transparent;
          border-right: unset;
          top: 0;
          left: unset;
          right: 0;
        }
      }
      .team-card__desc {
        width: calc(100% - 59.6875em);
        height: 100%;
        background: #E0E3E4;
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 3.125em;
      }
      .team-card__text {
        p {
          font-size: 1.375em;
          line-height: 150%;
        }
      }
    }
  }
  &__img {
    width: 26.4375em;
    min-height: 39.875em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-top: 6.25em solid #ffffff;
      border-right: 6.25em solid transparent;
      top: -1px;
      left: -1px;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc {
    width: calc(100% - 26.4375em);
    padding: 3.125em 0 3.125em 3.125em;
  }
  .inspiration__items{
    margin-top: .5625em;
  }
  &__text {
    margin-top: 3.125em;
    p {
      font-size: 1.125em;
      line-height: 150%;
    }
  }
}

.partners {
  padding: 3.125em 0 6.25em;
  &__slider {
    position: relative;
    margin-top: 3.125em;
  }
  @media screen and (min-width: 992px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(7,calc((100% / 7)));
      grid-auto-rows: 1fr;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
  }
  .swiper-slide {
    background: #FFFFFF;
    border: 0.0625em solid #F2F2F2;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.625em;
    img {
      max-width: 11.3125em;
      width: auto;
    }
  }
}


@media screen and (max-width: 992px) {
  .team {
    padding: 0.625em 0 1em;
    &__items {
      gap: 1.25em;
    }
  }

  .team-card {
    display: block;
    &__img {
      width: 100%;
      min-height: 25em;
      &:after {
        display: none;
      }
    }
    &__desc {
      width: 100%;
      padding: 1.25em 0 0;
    }
    &__text {
      margin-top: 1em;
      p {
        font-size: .875em;
      }
    }
  }

  .partners {
    padding: 1em 0 2em;
    &__slider {
      margin-top: 1.5625em;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-slide {
      height: auto;
      img {
        max-width: 7.5em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .team {
    &__items {
      grid-template-columns: 1fr;
    }
  }

  .team-card {
    &__img {
      min-height: 18.75em;
    }
  }

  .partners {
    .swiper-slide {
      img {
        max-width: 5em;
      }
    }
  }
}

