.services {
  padding: .625em 0 3.125em;
  &__cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.875em;
    margin-top: 1.875em;
  }
}

.services-card {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  &:nth-child(even) {
    .services-card__img {
      order: -1;
    }
    .services-card__img {
      &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-top: 1.875em solid #ffffff;
        border-left: 1.875em solid transparent;
        top: 0;
        right: 0;
      }
    }
  }
  &:nth-child(odd) {
    .services-card__img {
      &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-top: 1.875em solid #ffffff;
        border-right: 1.875em solid transparent;
        top: 0;
        left: 0;
      }
    }
  }
  &__desc {
    width: 31.25em;
    height: calc(100% - 1.875em);
    padding: 3.125em;
    background: #D9D9D9;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__img {
    width: calc(100% - 31.25em);
    position: relative;
    height: 33.125em;
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    text-transform: uppercase;
  }
  &__text {
    margin-top: 0.3125em;
  }
}

.team-card{
  &:nth-child(1){
    @media screen and (min-width: 993px){

    }
  }
}


@media screen and (max-width: 992px) {
  .services {
    padding-bottom: 1em;
    &__cards {
      gap: 1.25em;
    }
  }

  .services-card {
    flex-direction: column;
    &__img{
      padding-bottom: 75%;
      height: unset;
      img{
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    &__desc {
      width: 100%;
      height: auto;
      padding: 1.25em;
    }
    &__img {
      width: 100%;
      order: -1;
      &:after {
        display: none;
      }
    }
  }
}
