.space{
  padding: .625em 0 3.125em;
  &__top{
    max-width: 67.75em;
    @include grid100gap(.625em);
    .text46{
      max-width: 17em;
    }
  }
  &__wrap{
    position: relative;
    margin-top: -7.5em;
  }
  &__video{
    position: absolute;
    width: 56.25em;
    height: 30.625em;
    background-color: $primary;
    left: 6.25em;
    top: 11.9375em;
    padding: 4em;
    display: flex;
    align-items: flex-start;
    &-wrap{
      display: flex;
      align-items: center;
      text-transform: uppercase;
      color: #fff;
      &:hover{
        color: #fff;
      }
      img{
        width: 1.5em;
        margin-right: 1em;
      }
      span{
        font-size: 1.375em;
        line-height: 140%;
        letter-spacing: 0.05em;
      }
    }
  }
  &__img{
    pointer-events: none;
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
}

.space-info{
  padding: 3.125em 0;
  overflow: hidden;
  &__wrap{
    padding-bottom: 7.6875em;
  }
  &__grid{
    padding: 6.25em 3.125em;
    background: #F8F8F8;
    min-height: 39.375em;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-top: 1.875em solid #fff;
      border-right: 1.875em solid transparent;
      top: 0;
      left: 0;
    }
  }
  &__content{
    max-width: 32em;
  }
  @media screen and (min-width: 993px) {
    .space-slider {
      position: absolute;
      right: 3.125em;
      top: 10.75em;
      width: 50.3125em;
    }
    .catalog-card__img {
      height: 36.25em;

      &:after {
        right: unset;
        left: -1px;
        border-left: unset;
        border-top: 1.875em solid #F8F8F8;
        border-right: 1.875em solid transparent;
      }
    }
    .swiper-nav {
      left: 1.875em;
      bottom: 1.875em;
      right: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .space{
    overflow: hidden;
    padding-bottom: 1em;
    &__top{
      max-width: 14em;
    }
    .text46{
      font-size: 1.25em;
    }
    &__img{
      margin-top: 1.25em;
      img{
        height: 16em;
        width: auto;
        margin-left: -2em;
      }
    }
    &__video{
      position: static;
      padding: .625em 1.25em;
      height: unset;
      width: 100%;
      &-wrap{
        img{
          width: 1em;
          margin-right: .5em;
        }
        span{
          font-size: 1em;
        }
      }
    }
  }

  .space-info{
    padding: 1em 0;
    &__grid{
      padding: 0;
      background-color: transparent;
      &:after{
        display: none;
      }
    }
    .space-slider{
      margin-top: 1em;
    }
  }
}
