.footer{
  padding: 6.25em 0;
  background: #1D1D1D;
  color: #fff;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col{
    max-width: 26.375em;
  }
  &__logo{
    width: 12.1875em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1.375em;
    line-height: 140%;
    text-transform: uppercase;
    font-family: $font2;
    color: #515151;
    &._white{
      color: #fff;
    }
  }
  &__links{
    margin-top: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastMb(.625em);
  }
  .socials{
    margin-top: 1.25em;
  }
  &__form{
    margin-top: .625em;
    &-title{
      color: #515151;
    }
    &-fields{
      margin-top: .625em;
      input{
        background-color: transparent;
        border-color: #494949;
        color: #fff;
        @media screen and (min-width: 993px){
          height: 4em;
          &:focus,
          &.input-active {
            padding-top: 1.4em;
            &+label {
              font-size: .75em;
              top: 1em;
            }
          }
        }
      }
      label{
        color: #575757;
      }
    }
  }
  &__btn{
    margin-top: -2px;
    width: calc(100% - 4.4375em);
    background-color: transparent;
    border: 2px solid $primary;
    @media screen and (min-width: 993px){
      height: 4.4375em;
    }
    &:after{
      height: 4.4375em;
      width: 4.4375em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.5em 0;
    &__inner{
      @include grid100gap(1em);
    }
    &__title{
      font-size: 1.125em;
    }
    &__links{
      margin-top: .75em;
    }
    &__link{
      @include lastMb(.5em);
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__logo{
      width: 8em;
    }

  }
}
