.fg {
  position: relative;
  margin: 0;
  &.fg-stat{
    input{
      padding-top: 0 !important;
    }
  }
  label,
  input{
    text-transform: uppercase;
    font-size: 1.125em;
    line-height: 130%;
    font-family: $font2;
  }
  input{
    border: 2px solid #E0E3E4;
    border-radius: 27.8em;
    padding: 0 2.78em;
    height: 5.05em;
    width: 100%;
    color: $black;
    line-height: 150%;
    background-color: #FFFFFF;
    &::placeholder {
      color: rgba($black,.6);
    }
    &:hover,
    &:focus{
      border-color: $primary;
    }
    &:focus,
    &.input-active {
      padding-top: 1.7em;
      &+label {
        font-size: .75em;
        top: 1.67em;
        left: 4.17em;
        transform: translateY(0%);
      }
    }
  }
  label {
    transition: all .1s;
    pointer-events: none;
    position: absolute;
    left: 2.78em;
    top: 50%;
    transform: translateY(-50%);
    color: rgba($black,.6);
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    font-size: .616em;
  }
}
